.skl-ds-card .skl-ds-widget {
  padding: 8px 0;
  width: 33%;
}

.skl-ds-card-header {
  border: 1px solid black;
  border-radius: 16px 16px 0 0;
  padding: 6px 16px;
}

.skl-ds-card--important .skl-ds-card-header {
  background-color: red;
  border-color: red;
}
.skl-ds-card--important .skl-ds-card-body__infos .skl-ds-widget:last-child {
  background-color: red;
  border-radius: 8px;
}

.skl-ds-card-body {
  padding: 8px 16px;
}

